import firebase from 'firebase'
import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import Files from './views/Files.vue'
// import Login from './views/Login.vue'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '*',
      redirect: '/home'
    },
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: 'https://cagtransit.github.io/meetings',
      redirect: '/home'
    },
    {
      path: 'https://cagtransit.github.io/meetings/home',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: 'https://cagtransit.github.io/meetings/files',
      redirect: '/files'
    },
    {
      path: '/files',
      name: 'Files',
      component: Files,
      meta: {
        requiresAuth: false
      }
    }
  ]
});



router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if (requiresAuth && !currentUser) next('/');
  else if (!requiresAuth && currentUser) next({path: 'files'});
  else next();
});

export default router;
