<script>
  import firebase from 'firebase';
  import vuetify from 'vuetify'

  export default {
    name: 'login',
    data() {
      return {
        dialog: false,
        passwordShow: false,
        valid: true,
        email: '',
        emailRules: [
          v => !!v || 'Email is required',
          v => /.+@.+/.test(v) || 'Email must be valid'
        ],
        password: '',
        passwordRules: [
          v => !!v || 'Password is Required'
        ],
        pemail: '',
        passwordSnackbar: {
          visible: false,
          errorMessage: 'Password reset email has been sent!'
        },
      }
    },
    methods: {

      validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
          this.login()
        }
      },
      login: function() {
        firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
          () => {
            this.$router.replace('files')
          },
          (err) => {
            alert('Oops. ' + err.message)
          }
        );
      },
      resetPassword () {
      firebase.auth().sendPasswordResetEmail(this.pemail).then(() => {
        this.showResetPassword = false
        this.passwordSnackbar.visible = true
      })
    },
    }
  }
</script>

